import React from "react";
import {deleteLessonProgress} from "../../Redux/lessonsReducer";

const progressWindow = (props) => {

 return (
     <div>
         <button type="button" className="btn btn-dark" data-bs-toggle="modal" data-bs-target="#exampleModal">
             Сбросить прогресс
         </button>

         <div className="modal fade" id="exampleModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
             <div className="modal-dialog">
                 <div className="modal-content">
                     <div className="modal-header">
                         <h5 className="modal-title" id="exampleModalLabel">Внимание</h5>
                         <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                     </div>
                     <div className="modal-body">
                         Вы уверены, что хотите удалить весь прогресс по урокам? Его невозможно будет восстановить!
                     </div>
                     <div className="modal-footer">
                         <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Не удалять</button>
                         <button onClick={props.deleteLessonProgress} type="button" data-bs-dismiss="modal" className="btn btn-dark">Удалить</button>
                     </div>
                 </div>
             </div>
         </div>
     </div>
 )
}

export default progressWindow;