const GOOD_RESULT = 'GOOD-RESULT';
const REMOVE_RESULT_PROFILE = 'REMOVE-RESULT-PROFILE';

export const deleteLessonProgress = () => {
    return {
        type: REMOVE_RESULT_PROFILE
    }
}

export const goodLessonResult = (data) => {
    return {
        type: GOOD_RESULT,
        data
    }
}




