const lessonsReducer = (state = initialState, action) => {

    switch (action.type) {
        case GOOD_RESULT:
            return {
                ...state,
                progressLessons: [...state.progressLessons.filter(l => l.numLesson === action.data.numLesson)].length === 0 ?
                    [...state.progressLessons, action.data] : [...state.progressLessons]
            }

        case REMOVE_RESULT_PROFILE:
            return {
                ...state,
                progressLessons: []
            }

        default: return state
    }

}

export default lessonsReducer;
