import {connect} from "react-redux";
import {NavLink, withRouter} from "react-router-dom";
import React from "react";
import style from "./welcome.module.css"
import {AuthRedirect} from "../HookRedirect/redirect";
import NullImage from "../images/0.png";
import OneImage from "../images/1.png";

const WelcomePage = (props) => {
    return (
        <div className="container-lg center">
            <div className={style.wrapWelcome}>
                <div className={style.wrapImages}>
                <img src={NullImage} />

                <h2>Добро пожаловать в наше приложение!<br/>
                    Здесь ты научишься основам C#, Unity и другим технологиям.<br/>
            Желаем тебе удачи!</h2>
                    <img src={OneImage} />
                </div>
                <button type="button" className="btn btn-dark btn-lg"><NavLink className={style.text} to={'/lessons'}>Перейти к урокам</NavLink></button>
            </div>
        </div>
    )
}

let MapStateToProps = (state) => {
    return {

    }
}

export default connect(MapStateToProps, {

}, withRouter, AuthRedirect) (WelcomePage);