import {useState} from "react";
import style from './controls.module.css';
import {NavLink} from "react-router-dom";

export const ControlsForm = ({title, handleClick, messageError, setMessageError}) => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    return <div className={style.wrap}>
        {
            <div className="align-self-xl-center">

                <div className="container-lg overflow-hidden">

                    <h2>{title}</h2>
                    <div className="mb-6">
                        <label htmlFor="exampleInputEmail1" className="form-label">E-MAIL</label>
                        <input onInput={() => setMessageError('')} onClick={() => setMessageError('')} type="email" value={email} onChange={(e) => {
                            setEmail(e.target.value)
                        }} placeholder="email" className="form-control" id="exampleInputEmail1"
                               aria-describedby="emailHelp"/>
                        <div id="emailHelp" className="form-text">
                        </div>
                    </div>
                    <div className="mb-3">
                        <label htmlFor="exampleInputPassword1" className="form-label">PASSWORD</label>
                        <input onInput={() => setMessageError('')} onClick={() => setMessageError('')} type="password" value={password} onChange={(e) => {
                            setPassword(e.target.value)
                        }} placeholder="password" className="form-control" id="exampleInputPassword1" />
                    </div>
                    <div className={style.registerBlock}>
                        <button className="btn btn-dark" onClick={() => handleClick(email, password)}>
                            Подтвердить
                        </button>
                        <span className={style.spanButtons}>
                        <button type="button" className="btn btn-outline-dark">{title === "Авторизация" ? <NavLink className={style.outButton} to={'/registration'}>Регистрация</NavLink> :
                            <NavLink className={style.outButton} to={'/login'}>Авторизация</NavLink>}</button>
                        </span>
                        <span className={style.errorStyle}>{messageError}</span>
                        <br/>
                    </div>
                    <br/>
                </div>
            </div>
        }
    </div>
}