import {useHistory, withRouter} from "react-router-dom";
import {connect} from "react-redux";
import React from "react";
import {compose} from "redux";
import {ControlsForm} from "../ControlsUser/ControlsForm";
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import {setMessageError, setUser} from "../../Redux/userReducer";
import { getStorage, ref, getDownloadURL } from "firebase/storage";


const Login = (props) => {
    const {push} = useHistory()
    const handleLogin = (email, password) => {
        const auth = getAuth();
        signInWithEmailAndPassword(auth, email, password).then(({user}) => {

            props.setUser(user.email, user.accessToken, user.uid, user.photoURL)
            push('/welcome')
        }).catch((error) => {
                let msgError = '';
                if (error.code === 'auth/too-many-requests') {
                    msgError = 'Слишком много запросов!'
                } else if (error.code === 'auth/wrong-password' || error.code === 'auth/user-not-found') {
                    msgError = 'Неверный логин или пароль!'
                } else if (error.code === 'auth/invalid-email') {
                    msgError = 'Введите данные!'
                } else {
                    msgError = 'Неизвестная ошибка: ' + error.code
                }
                props.setMessageError(msgError)
            }
        )
    }
    return <ControlsForm title={"Авторизация"} handleClick={handleLogin} messageError={props.messageError} setMessageError={props.setMessageError} />
}

let MapStateToProps = (state) => {
    return {
        messageError: state.UserData.messageError
    }
}

export default compose(
    connect(MapStateToProps, {
        setUser, setMessageError
    })
    ,withRouter) (Login);