import React from "react";
import './App.css';
import {Redirect, Route, Switch} from "react-router-dom";
import Header from "./Components/Header/header";
import WelcomePage from "./Components/WelcomePage/welcome";
import ListLessons from "./Components/Lessons/ListLessons";
import LessonStart from "./Components/Lessons/lessonStart";
import ProfilePage from "./Components/Profile/profile";
import About from "./Components/About/about";
import Login from "./Components/Login/login"
import Registration from "./Components/Registration/registration"
import Fon from './Components/images/fon1.png'

const App = (props) => {

  return (
      <div style={{
          backgroundImage: `url(${ Fon })`,
          backgroundRepeat  : 'no-repeat',
          backgroundPosition: 'center',
          backgroundSize: 'cover',
          height: '1780px',

      }}>

      <Header />
      <Switch>
        <Route exact path='/' render={() => <Redirect to={'/login'} />}/>
        <Route path='/lesson/:number' component={LessonStart}  />
        <Route path='/welcome' component={WelcomePage} />
        <Route path='/lessons' component={ListLessons} />
        <Route path='/profile' component={ProfilePage} />
        <Route path='/about' component={About} />
        <Route path='/login' component={Login} />
        <Route path='/registration' component={Registration} />

      </Switch>
      </div>
  );

}

export default App;
