import React from "react";
import style from "./lessons.module.css";
import {NavLink} from "react-router-dom";

const LessonCard = (props) => {
    let data = props.data
    return (
        <div className={style.wrapCard}>
            <div className="card">
                <div className="card-body">
                    <h5 className="card-title">{data.title}</h5>
                    <p className="card-text">Номер урока {data.numberLesson} <br/>
                        {data.shortDescription}<br/>
                        Количество возможных баллов за урок - {data.points}</p>
                 <NavLink  className="btn btn-dark" to={'/lesson/' + data.numberLesson}>Начать урок</NavLink>
                </div>
            </div>
        </div>
    )
}

export default LessonCard;