const SET_USER = 'SET-USER';
const REMOVE_USER = 'REMOVE-USER';
const MESSAGE_ERROR = 'MESSAGE-ERROR';
const ADD_PHOTO_PROFILE = 'ADD-PHOTO-PROFILE';

export const addPhotoProfile = (photoURL) => {
    return {
        type: ADD_PHOTO_PROFILE,
        photoURL
    }
}

export const setMessageError = (msgError) => {
    return {
        type: MESSAGE_ERROR,
        msgError
    }
}

export const setUser = (email, token, id, photoURL) => {
    return {
        type: SET_USER,
        email,
        token,
        id,
        photoURL
    }
}

export const removeUser = () => {
    return {
        type: REMOVE_USER
    }
}

let initialState = {
    messageError: '',
    isAuth: false,
    email: null,
    token: null,
    id: null,
    photoURL: null
}

const userReducer = (state = initialState, action) => {
    switch (action.type) {

        case ADD_PHOTO_PROFILE: {
            return {
                ...state,
                photoURL: action.photoURL
            }
        }

        case MESSAGE_ERROR: {
            return {
                ...state,
                messageError: action.msgError
            };
        }

        case SET_USER:
            return {
                ...state, isAuth: !!action.email, email: action.email, token: action.token, id: action.id, photoURL: action.photoURL
            }
        case REMOVE_USER:
            return {
                ...state,
                isAuth: false, email: null, token: null, id: null, photoURL: null
            }
        default: return state;
    }
}

export default userReducer;