import style from "../WelcomePage/welcome.module.css";
import {NavLink, withRouter} from "react-router-dom";
import React from "react";
import {compose} from "redux";
import {connect} from "react-redux";
import {removeUser} from "../../Redux/userReducer";
import {AuthRedirect} from "../HookRedirect/redirect";

const About = (props) => {
    return (
        <div className="container-lg center">
            <br />
            <div className="card">
                <div className="card-body">
                    Информация о нас
                </div>
            </div>
            <br />
            <h4>Контактный почтовый ящик: gamelearn@game.com</h4>
            <h4>Контактный телефон: +7(900)-(888)-(888)</h4>
            <br/>
            <h5>
                Мы большая компания, занимающая передовые места в области IT. Наша специализация - это разработка уроков с элементами геймификации.
                Сотни тысяч пользователей видят в нас надежность, безопасность и преимущество. <br/><br/> Поэтому они пользуются нашими уроками.
                У нас вы сможете найти большой спектр уроков для новичков и заканчивая сложными алгоритмами с законами математики и физики.
                Наши уроки являются бесплатными и доступны сразу после регистрации.
            </h5>

            <div className="card">
            <div className="card-body">
                Часто задаваемые вопросы
            </div>
            </div>
            <div className="accordion" id="accordionExample">
                <div className="accordion-item">
                    <h2 className="accordion-header" id="headingOne">
                        <button className="accordion-button" type="button" data-bs-toggle="collapse"
                                data-bs-target="#collapseOne" aria-expanded="false" aria-controls="collapseOne">
                            Как обнулить свой прогресс по урокам?
                        </button>
                    </h2>
                    <div id="collapseOne" className="accordion-collapse collapse" aria-labelledby="headingOne"
                         data-bs-parent="#accordionExample">
                        <div className="accordion-body">
                            Нужно перейти в личный кабинет. После этого под прогрессом нажать на кнопку <strong>Сбросить прогресс</strong>
                        </div>
                    </div>
                </div>
                <div className="accordion-item">
                    <h2 className="accordion-header" id="headingTwo">
                        <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                                data-bs-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
                            Могу ли я зарегистрировать еще один аккаунт?
                        </button>
                    </h2>
                    <div id="collapseTwo" className="accordion-collapse collapse" aria-labelledby="headingTwo"
                         data-bs-parent="#accordionExample">
                        <div className="accordion-body">
                            По определенным целям, У некоторых людяй есть нужда заводить второй аккаунт. Это не возбраняется.
                        </div>
                    </div>
                </div>
                <div className="accordion-item">
                    <h2 className="accordion-header" id="headingThree">
                        <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                                data-bs-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
                            Могу ли я удалить свой аккаунт?
                        </button>
                    </h2>
                    <div id="collapseThree" className="accordion-collapse collapse" aria-labelledby="headingThree"
                         data-bs-parent="#accordionExample">
                        <div className="accordion-body">
                            Удаление профиля в автоматическом режиме не предусмотрено, поскольку имеет малый спрос.
                            Чтобы удалить аккаунт напишите на контакную почту логин от нашего сервиса и свою персональную информацию
                            (ФИО)
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}

let MapStateToProps = (state) => {
    return {

    }
}

export default compose(
    connect(MapStateToProps, {
        removeUser
    })
    ,withRouter, AuthRedirect) (About);