import style from "./lessons.module.css";
import {NavLink, useHistory, withRouter} from "react-router-dom";
import React, {useEffect, useState} from "react";
import {connect} from "react-redux";
import {compose} from "redux";
import ModalWindow from './modalWindow';
import {goodLessonResult} from "../../Redux/lessonsReducer";
import {AuthRedirect} from "../HookRedirect/redirect";
import Game from "../components/state/Game";

const LessonStart = ({match, ...props}) => {

    const {push} = useHistory();
    let [localLesson, setLocalLesson] = useState(props.lessons.filter(l => Number(l.numberLesson) === Number(match.params.number))[0]);
    let [prevDisable, setPrevDisable] = useState(false);
    let [nextDisable, setNextDisable] = useState(false);
    let [answerLocal, setAnswerLocal] = useState('');

    let onAnswerLocal = (e) => {
        setAnswerLocal(e.target.value)
    }

    let previousPage;
    let nextPage;

    let changeLesson = (e) => {
        let countPage = Number(match.params.number);
        let countPage2 = Number(match.params.number);

        previousPage = countPage += -1;
        if (previousPage < 1) {
            previousPage = 1;

        }
        nextPage = countPage2 += 1;
        if (nextPage > props.lessons.length) {
            nextPage = props.lessons.length;
        }

        if (previousPage > 0 && nextPage <= props.lessons.length) {
            e.target.value === "next" ? push('/lesson/' + nextPage) : push('/lesson/' + previousPage)
        }
    }

    useEffect(() => {
        setLocalLesson(props.lessons.filter(l => Number(l.numberLesson) === Number(match.params.number))[0])
        Number(match.params.number) === 6 ? setNextDisable(true) : setNextDisable(false);
        Number(match.params.number) === 1 ? setPrevDisable(true) : setPrevDisable(false);
    }, [changeLesson])
    return (

        <div className="container-lg center">

            <div className={style.lessonContainer}>
                <h2 className={style.titleLesson}>{localLesson.title + " №" + localLesson.numberLesson}</h2>
                <br/>
                <span className={style.descriptionInfo}>{localLesson.description}</span>
            </div>

            {/*<div className="mb-3">*/}
            {/*    <label htmlFor="formGroupExampleInput" className="form-label">Ваш ответ</label>*/}
            {/*    <input onChange={onAnswerLocal} value={answerLocal} type="text" className="form-control" id="formGroupExampleInput"*/}
            {/*           placeholder="Введите правильный ответ" />*/}
            {/*</div>*/}

            {/*<ModalWindow localLesson={localLesson} goodLessonResult={props.goodLessonResult} answer={localLesson.answer}*/}
            {/*             answerLocal={answerLocal} setAnswerLocal={setAnswerLocal} nextPage={nextPage}*/}
            {/*             setLocalLesson={setLocalLesson} lessonsLength={props.lessons.length}*/}
            {/*             num={localLesson.numberLesson}/>*/}

            <div className="container-lg center">
            <Game localLesson={localLesson} goodLessonResult={props.goodLessonResult} />
            </div>
            <span> Количество возможных баллов за прохождение урока: {localLesson.points}</span>
            <div className={style.navigatorLessons}>
                <button onClick={changeLesson} disabled={prevDisable} value={"prev"} type="button" className="btn btn-dark">Предыдущий урок</button>
                <button onClick={changeLesson} disabled={nextDisable} value={"next"} type="button" className="btn btn-dark">Следующий урок</button>
            </div>

        </div>
    )
}
let MapStateToProps = (state) => {
    return {
        lessons: state.ListLessons.lessons
    }
}

export default compose(
    connect(MapStateToProps, {
        goodLessonResult
       })
    ,withRouter, AuthRedirect) (LessonStart);