import React from "react";
import {NavLink, withRouter} from "react-router-dom";
import {connect} from "react-redux";
import style from "./header.module.css";
import { FiBox } from "react-icons/fi";
import {compose} from "redux";
import {removeUser} from "../../Redux/userReducer";
import {AuthRedirect} from "../HookRedirect/redirect";

const Header = (props) => {
    return (
        <div>
            <nav className="navbar navbar-dark bg-dark">
                <div className="container-fluid">

                    <a className="navbar-brand" href="#">GAME LESSONS <FiBox /></a>
                    <div>
                     <img width="30" src={props.photoURL} />
                    <button onClick={props.removeUser} type="button" className="btn btn-dark">ВЫЙТИ</button>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse"
                            data-bs-target="#navbarText" aria-controls="navbarText" aria-expanded="false"
                            aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    </div>
                    <div className="collapse navbar-collapse" id="navbarText">
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                                <NavLink className={style.text} to={'/profile'}>Личный кабинет</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className={style.text} to={'/lessons'}>Уроки</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className={style.text} to={'/about'}>О нас</NavLink>
                            </li>
                        </ul>

                    </div>
                </div>
            </nav>

        </div>
    )
}

let MapStateToProps = (state) => {
    return {
        email: state.UserData.email,
        photoURL: state.UserData.photoURL
    }
}

export default compose(
    connect(MapStateToProps, {
        removeUser
    })
    ,withRouter, AuthRedirect) (Header);