import {useHistory, withRouter} from "react-router-dom";
import {connect} from "react-redux";
import React from "react";
import {compose} from "redux";
import {ControlsForm} from "../ControlsUser/ControlsForm";
import { getAuth, createUserWithEmailAndPassword } from "firebase/auth";
import {setMessageError, setUser} from "../../Redux/userReducer";

const Login = (props) => {
    const {push} = useHistory()
    const handleLogin = (email, password) => {
        const auth = getAuth();
        createUserWithEmailAndPassword(auth, email, password)
            .then((userCredential) => {
                const user = userCredential.user;
                props.setUser(user.email, user.accessToken, user.uid)
                push('/welcome')

            })
            .catch((error) => {
                const errorCode = error.code;
                const errorMessage = error.message;
                // ..
            });
    }
    return <ControlsForm title={"Регистрация"} handleClick={handleLogin} messageError={props.messageError} setMessageError={props.setMessageError} />
}

let MapStateToProps = (state) => {
    return {
        messageError: state.UserData.messageError
    }
}

export default compose(
    connect(MapStateToProps, {
        setUser, setMessageError
    })
    ,withRouter) (Login);