import React, {Component, useEffect, useState} from 'react'
import Target from './Target'
import {defaultParams, initialGameState} from '../../js/params.js'
import {gameField, panelStyle, messageStyle} from '../../js/styles.js'
import {useHistory} from "react-router-dom";
let gameParams = {...defaultParams}

class Game extends React.Component {

    runNewGame = () => {
        this.setState({ ...initialGameState(), gameover: false, start: true, targetsCnt: 0})
        this.makeGameFlow(this.gameOptions())
            }

    gameOptions = () => ({
        probability: this.state.probability,
        periodMsec: gameParams.periodMsec,
        answers: this.props.localLesson.answers
    })

    makeGameFlow = (options, numLesson) => {
        if (this.state.life === 0) {
            gameParams = {...defaultParams}
            const score = this.state.score
            this.setState({...initialGameState(), gameover: true, lastscore: this.state.score})
            return false
        } else if (this.state.score === 5) {
            this.props.goodLessonResult({numLesson: Number(this.props.localLesson.numberLesson), points: Number(this.props.localLesson.points)})
            this.setState({...initialGameState(), start: false, gameover: true, lastscore: this.state.score})
            return false
        }
        let gameIterator = this.targetGenerate(options)
        setTimeout(
            () => {
                clearInterval(gameIterator)
                options.periodMsec -= gameParams.difficultStepMsec
                options.probability += gameParams.difficultStepProbability
                this.makeGameFlow(options, this)
            },
            gameParams.difficultIntervalMsec
        )
    }

    targetGenerate = ({probability, periodMsec, answers, gameIterator}) => {
        return setInterval(() => {

                if (this.state.numLesson !== this.props.localLesson.id) {
                    this.setState({...initialGameState(), gameover: false, start: false, targetsCnt: 0, numLesson: this.props.localLesson.id})

                    return false
                }

                for (let i = 0; i < answers.length; i++) {
                    if (this.state.numLesson !== this.props.localLesson.id) {
                        this.setState({...initialGameState(), gameover: false, start: false, targetsCnt: 0, numLesson: this.props.localLesson.id})

                        return false
                    }
                    if (Math.random() * 100 <= probability) {
                        const xpos = Math.random() * (gameParams.fieldWidth - gameParams.targetWidth)
                        const ypos = Math.random() * (gameParams.fieldHeight - gameParams.targetHeight)
                        this.setState({
                            targets: [
                                ...this.state.targets,
                                <Target
                                    id = {this.state.targetsCnt}
                                    key = {this.state.targetsCnt}
                                    title={answers[i]}
                                    coordinate = {{xpos, ypos}}
                                    clickHandler = {this.clickTarget}
                                    targetFired = {() => {this.targetFired(answers[i])}}
                                />
                            ],
                            targetsCnt: ++this.state.targetsCnt
                        })
                    }
                }
            }, periodMsec)


    }

    clickTarget = (id) => {
        let goodAnswer = this.props.localLesson.goodAnswer
        console.log(id)
        const _targets = [...this.state.targets]
        _targets.reduce(
            (acc, curr, i, arr) => {

                if (curr.props.id === id && curr.props.title === goodAnswer) {
                    arr.splice(i, 1)
                    this.setState({
                        score: ++this.state.score,
                        targets: _targets
                    })
                }

                if(curr.props.id === id && curr.props.title !== goodAnswer) {
                    const life = this.state.life > 0
                        ? this.state.life - 1 : 0
                    arr.splice(i, 1)
                    this.setState({
                        targets: _targets,
                        life: life
                    })
                }
            },
            _targets[0]
        )
    }

    targetFired = (answer) => {

        if (answer === this.props.localLesson.goodAnswer) {
            const life = this.state.life > 0
                ? this.state.life - 1
                : 0
            this.setState({life: life})
        }

        const _targets = [...this.state.targets]
        _targets.reduce(
            (acc, curr, i, arr) => {
                    arr.splice(i, 1)
                    this.setState({
                        targets: _targets
                    })
            }, _targets[0]
        )
    }


    constructor(props) {
        super(props)
        this.state = {...initialGameState(), start: false, numLesson: this.props.localLesson.id}
    }



    render() {
        if (this.state.numLesson !== this.props.localLesson.id) {
            this.setState({...initialGameState(), gameover: false, start: false, targetsCnt: 0, numLesson: this.props.localLesson.id})

            return false
        }
        return (

            <div>
                <div style={panelStyle}>
                    {   this.state.gameover !== true
                        && this.state.start === true
                        && ` Попыток осталось: ${this.state.life} Попаданий: ${this.state.score}`

                    }
                </div>
                <div className="game-field" style={gameField}>

                    {

                        this.state.start === false
                            &&
                        <div
                            style={messageStyle}
                            onClick={() => this.runNewGame()}
                        >

                            <span style={{cursor: 'pointer'}}>Начать игру </span>
                        </div>
                    }

                    {
                        this.state.gameover === true

                        && <div style={messageStyle}>
                            {this.state.lastscore === 5 ? <div>Вы прошли урок!<br/>
                            За урок получено: {this.props.localLesson.points} points
                            </div> : <div>К сожалению вы не прошли урок :(</div>}
                            <div>Мишеней сбито: {this.state.lastscore}</div>
                            <div onClick={() => this.runNewGame()}><span
                                style={{textDecoration: 'none', cursor: 'pointer'}}>Играть еще</span>
                            </div>
                        </div>
                    }

                    { this.state.targets }

                </div>
            </div>
        )
    }
}

export default Game;