import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import React, {useState} from "react";
import ProgressWindow from "./progressWindow";
import styles from './profile.module.css';
import {deleteLessonProgress} from "../../Redux/lessonsReducer";
import {compose} from "redux";
import {AuthRedirect} from "../HookRedirect/redirect";
import {addPhotoProfile} from "../../Redux/userReducer";
import {getAuth, updateProfile} from "firebase/auth";
import { getDownloadURL, getStorage, ref, uploadBytes } from "firebase/storage";
const ProfilePage = (props) => {

    let localPoints = 0;
    let allPoints = 0;

    const auth = getAuth();
    const storage = getStorage();

    for (let i = 0; i < props.progressLessons.length; i++) {
        localPoints += props.progressLessons[i].points;
    }

    for (let i = 0; i < props.lessons.length; i++) {
        allPoints += props.lessons[i].points;
    }

     async function upload(file, currentUser) {
        const fileRef = ref(storage, 'images/'+currentUser.uid+'.png');
        const snapshot = await uploadBytes(fileRef, file);
        const photoURL = await getDownloadURL(fileRef);

        updateProfile(currentUser, {photoURL});

         props.addPhotoProfile(photoURL)
    }


    let onChangeFile = (e) => {
        if (e.target.files[0]) {
            upload(e.target.files[0], auth.currentUser);
        }


    }

    return (
        <div className="container-lg center">
            <div className={styles.profileText}>
                <h2>ЛИЧНЫЙ КАБИНЕТ</h2>
                <div className={styles.avaAndEmail}>
                    <img className={styles.imgProfile} src={props.photoURL}></img>
                    <h3>{props.email}</h3>
                </div>

            </div>
            <br/>
            <h3>Ваш прогресс по прохождению уроков</h3>
            <div className="progress" style={{height: 40 + 'px'}}>
                <div className="progress-bar bg-dark" role="progressbar" style={{width: localPoints}} aria-valuenow={localPoints}
                     aria-valuemin="0" aria-valuemax={allPoints}>{localPoints}</div>
            </div>
            <br/>
            <ProgressWindow deleteLessonProgress={props.deleteLessonProgress} />
            <br/>
            <div className="input-group mb-3">
                <input onChange={onChangeFile} type="file" className="form-control" id="inputGroupFile02" />
                    <label className="input-group-text" htmlFor="inputGroupFile02">Загрузить аватар профиля</label>
            </div>

        </div>
    )
}

let MapStateToProps = (state) => {
    return {
        progressLessons: state.ListLessons.progressLessons,
        lessons: state.ListLessons.lessons,
        photoURL: state.UserData.photoURL,
        email: state.UserData.email
    }
}

export default compose(
    connect(MapStateToProps, {
        deleteLessonProgress, addPhotoProfile
    })
    ,withRouter, AuthRedirect) (ProfilePage);